$Dark-desaturated-cyan/*(graphic design text)*/: hsl(167, 40%, 24%);
$Dark-blue/*(photography text)*/: hsl(198, 62%, 26%);
$Very-dark-desaturated-blue: hsl(212, 27%, 19%);
$Yellow: hsl(51, 100%, 49%);



.Image-Container--img {
  width: 100%;
}

.AboutUs-Container {
    padding: 15% 5%;
    padding-bottom: 10%;
}

.AboutUs-Container  {

   .AboutUs-Container__title {
      margin-bottom: 10%;
   }

   .AboutUs-Container__description {
    margin-bottom: 10%;
   }

}

// Services

.cherries {
  padding-left: 5%;
  padding-right: 5%;
  height: 71.5vh;
  background-size: 100%;
  background-repeat: no-repeat;
}

.orange {
  padding-left: 5%;
  padding-right: 5%;
  height: 71.5vh;
  background-size: 100%;
  background-repeat: no-repeat;
}

.cherries {
  background-image: url('./images/mobile/image-graphic-design.jpg');

}

.orange {
  background-image: url('./images/mobile/image-photography.jpg');
}

.cherries {
  h2 {
      color: $Dark-desaturated-cyan;
  }

  p {
    color: $Dark-desaturated-cyan;
  }
}

.orange {
  h2 {
    color: $Dark-blue;
  }

  p {
    color: $Dark-blue
  }
}

.ServiceText {
  position: relative;
  top: 65%;
}

.ServiceText {
  p {
    margin-top: 5%;
  }
}

// Testimonialsss

.Testimonial-Section {
  margin-top: 10%;
}

.Testimonial-Section {

}

.Testimonial-Container {
  padding: 0 5%;
  margin-top: 12%;
  margin-bottom: 15%;
} 

.Testimonial-Container__img-Container {
  margin-bottom: 7%;
}

.Testimonial-Container__img-Container {

  .Testimonial-Container__img-Container--img {
  height: 50px;
  border-radius: 30px;
} 

}

.Testimonial-Container__Review-Container {
  margin-bottom: 7%;
}


.Testimonial-Container__Name-Container--name {
  margin-bottom: 3%;
}

.Testimonial-Container__Name-Container--text {
  width: 1;
}

// Thumbnails

.Thumbnails-Section__ContainerPC {
  display: none;
}

.Thumbnails-Container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.Thumbnails-Container {
    img {
      width: 100%;
    }
}

@media(min-width: 375px) {
  .cherries {
    height: 93.5vh;
  }

  .orange {
    height: 93.5vh;
  }
}

@media(min-width: 425px) {
  .cherries {
    height: 106.5vh;
  }

  .orange {
    height: 106.5vh;
  }
}

@media(min-width: 768px) {

  .Nav-Container {
    padding-top: 3%;
  }

 main {
   .Transform-Container {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .Transform-Container {
    .Image-Container {
      order: 1;
    }

    .eggPC {
      display: inherit;
    }
  }

  .StandOut-Container {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .cupPC {
    display: inherit;
  }

  .Services-Container {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .cherries {
    background-image: url('./images/desktop/image-graphic-design.jpg');
    height: 65.5vh;
  }

  .orange {
    background-image: url('./images/desktop/image-photography.jpg');
    height: 65.5vh;
  }

  .ServiceText  {
    top: 45%;
  }

  .Testimonial-Section {
    margin-top: 5%;
    margin-bottom: 8%;
  }

  .Testimonial-Section {
    .Testimonial-Section__Container {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
    }
}

.Thumbnails-Container {
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

}}

@media(min-width: 1024px) {
  .Nav-Container {
    padding-top: 2%;
    padding-left: 3%;
    padding-right: 3%;
  }

  .Nav-Container--items {
    .Menu-Content__Contact {
      width: 20%;
    }
  }

  .WeAre-Container {
    height: 65vh;
  }

  .WeAre-Container {
    .WeAre--title {
      margin-top: -45%;
    }

    .WeAre--img {
      width: 70%;
    }
  }

  .AboutUs-Container {
    padding: 15%;
  }

  .AboutUs-Container {
    h2, p, h5 {
      text-align: left;
    }
  }

  .Services-Container {
    .cherries, .orange {
      .ServiceText {
        top: 53%;
      }
    }

    Footer {
      padding-top: 5%;
    }
  }


}

@media(min-width: 1440px) {
   .Nav-Container--items {
    justify-content: space-evenly;
    margin-right: -5%;
  }

  .Nav-Container--items {
    .Menu-Content__Contact {
      width: 15%;
    }
  }

  .WeAre-Container {
    height: 70vh
  }

  .WeAreContainer {
    .WeAre--title {
      margin-top: -45%;
    }

    .WeAre-Container__img-container {
      padding-bottom: 9%;
    }
      
    }


  .AboutUs-Container {
    h2 {
      font-size: xxx-large;
    }

    h2, h5 {
      color: $Very-dark-desaturated-blue;
    }
    
  }

  .Services-Container {
    .cherries, .orange {
      .ServiceText {
        top: 60%;
      }
    }

  .Transform-Container {
    h5 {
      text-decoration: underline $Yellow 5px;
    }
  }}}